.App {
  text-align: center;
}

.App-logo {
  height: 75vmin;
  pointer-events: none;
  z-index: 2;
}

.App-header {
  position:relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden;
}

.Eye-container {
  position: absolute;
  z-index: 3;
  height: 75vmin;
}

.Eye {
  position: relative;
  height: 75vmin;
}

@keyframes spotlight {
  0% {
    r: 4.1%;
    fill-opacity: 85%
  }

  to {
    r: 400%;
    fill-opacity: 100%
  }
}

@keyframes glow {
  0% {
    fill-opacity: 0
  }

  10% {
    fill-opacity: .08
  }

  15% {
    fill-opacity: .08
  }

  25% {
    fill-opacity: 0
  }

  40% {
    fill-opacity: 0
  }

  45% {
    fill-opacity: .1
  }

  55% {
    fill-opacity: .1
  }

  65% {
    fill-opacity: 0
  }

  85% {
    fill-opacity: 0
  }

  to {
    fill-opacity: .15
  }
}

@keyframes items {
  0% {
    background-color: #ffffffb3
  }

  to {
    background-color: #0003
  }
}

@keyframes appear {
  0% {
    fill-opacity: 1
  }

  to {
    fill-opacity: 0
  }
}

.bigTitle {
  font-family: "League Spartan", sans-serif;
  font-weight: 700;
  font-size: 15vw;
  text-decoration: underline;
  text-decoration-color: red;
  text-decoration-thickness: 2vh;
  line-height: 33vh;
}

.medTitle {
  font-family: "League Spartan", sans-serif;
  font-weight: 300;
  font-size: 6.5vw;
}

.Spotlight {
  position: absolute;
  /*height: 15vh;*/
  z-index: 0;
  /*overflow:hidden;*/
  /*animation-name: spotlight;*/
  /*animation-fill-mode: forwards;*/
  /*animation-duration: 1.5s;*/
  /*will-change: transform;*/
}

.circle {
  animation-name: spotlight;
  animation-fill-mode: forwards;
  animation-duration: 1.5s;
  will-change: transform;
  r: 5%;
}

.glow {
  r: 5%;
  animation: glow 1.1s 1 forwards running;
  animation-delay: .2s;
  fill: yellow;
  fill-opacity: 0
}

.hider {
  r: 5%;
  animation: appear .5s 1.8s 1 forwards running
}

.App-link {
  color: #61dafb;
}

.About-button {
  animation: items 1s 1 forwards running;
  position: absolute;
  top: 9%;
  left: 53%;
  padding-left: 3vw;
  padding-right: 3vw;
  border-radius: 1vmin;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}

.Things-button {
  animation: items 1s 1 forwards running;
  position: absolute;
  top: 60%;
  left: 15%;
  padding-left: 3vw;
  padding-right: 3vw;
  border-radius: 1vmin;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}

.Projects-button {
  animation: items 1s 1 forwards running;
  position: absolute;
  top: 80%;
  left: 70%;
  padding-left: 3vw;
  padding-right: 3vw;
  border-radius: 1vmin;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}


.Button-text {
  animation: button-colour 0.4s 1 forwards;
  font-family: "League Spartan", sans-serif;
}

.Hint-text {
  position:absolute;
  top: 90%;
  color: lightgray;
  animation: fadeInAnimation ease 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes button-colour {
  0% {
    color:black;
  }

  100% {
    color:white;
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}